@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,200,300,600,700,900);

a.btn {
    background: #0096a0;
    border-radius: 4px;
    -webkit-box-shadow: 0 2px 0px 0 rgba(0, 0, 0, 0.25);
    box-shadow: 0 2px 0px 0 rgba(0, 0, 0, 0.25);
    color: #ffffff;
    display: inline-block;
    padding: 6px 30px 8px;
    position: relative;
    text-decoration: none;
    -webkit-transition: all 0.1s 0s ease-out;
    -o-transition: all 0.1s 0s ease-out;
    transition: all 0.1s 0s ease-out;
}
.no-touch a.btn:hover {
    background: #00a2ad;
    -webkit-box-shadow: 0px 8px 2px 0 rgba(0, 0, 0, 0.075);
    box-shadow: 0px 8px 2px 0 rgba(0, 0, 0, 0.075);
    -webkit-transform: translateY(-2px);
    -ms-transform: translateY(-2px);
        transform: translateY(-2px);
    -webkit-transition: all 0.25s 0s ease-out;
    -o-transition: all 0.25s 0s ease-out;
    transition: all 0.25s 0s ease-out;
}
.no-touch a.btn:active,
a.btn:active {
    background: #008a93;
    -webkit-box-shadow: 0 1px 0px 0 rgba(255, 255, 255, 0.25);
    box-shadow: 0 1px 0px 0 rgba(255, 255, 255, 0.25);
    -webkit-transform: translate3d(0, 1px, 0);
    transform: translate3d(0, 1px, 0);
    -webkit-transition: all 0.025s 0s ease-out;
    -o-transition: all 0.025s 0s ease-out;
    transition: all 0.025s 0s ease-out;
}
div.cards {
  /*  margin: 280px auto;
    margin-top: 40%;
    */
    max-width: 100%;
    text-align: center;
}
div.card {
    background: #F04F54;
    display: inline-block;
    margin: 8px;
    max-width: 300px;
    -webkit-perspective: 1000;
    perspective: 1000;
    position: relative;
    color: black;
    text-align: left;
    -webkit-transition: all 0.3s 0s ease-in;
    -o-transition: all 0.3s 0s ease-in;
    transition: all 0.3s 0s ease-in;
    z-index: 1;
}
div.card img {
    max-width: 300px;
}
div.card div.card-title {
    background: #ffffff;
    padding: 6px 15px 10px;
    position: relative;
    z-index: 0;
}
div.card div.card-title a.toggle-info {
    border-radius: 32px;
    height: 32px;
    padding: 0;
    position: absolute;
    right: 15px;
    top: 10px;
    width: 32px;
}

div.card div.card-title a.toggle-info span {
    background: #ffffff;
    display: block;
    height: 2px;
    position: absolute;
    top: 16px;
    -webkit-transition: all 0.15s 0s ease-out;
    -o-transition: all 0.15s 0s ease-out;
    transition: all 0.15s 0s ease-out;
    width: 12px;
}
div.card div.card-title a.toggle-info span.left {
    right: 14px;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
        transform: rotate(45deg);
}
div.card div.card-title a.toggle-info span.right {
    left: 14px;
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
        transform: rotate(-45deg);
}
div.card div.card-title h2 {
    font-size: 24px;
    font-weight: 700;
    letter-spacing: -0.05em;
    max-width: 230px;
    margin: 0;
    padding: 0;
}
div.card div.card-title h2 small {
    display: block;
    font-size: 18px;
    font-weight: 600;
    letter-spacing: -0.025em;
}
div.card div.card-description {
    padding: 0 15px 10px;
    position: relative;
    font-size: 14px;
}
div.card div.card-actions {
    -webkit-box-shadow: 0 2px 0px 0 rgba(0, 0, 0, 0.075);
    box-shadow: 0 2px 0px 0 rgba(0, 0, 0, 0.075);
    padding: 10px 15px 20px;
    text-align: center;
}
div.card div.card-flap {
    background: #d9d9d9;
    position: absolute;
    width: 100%;
    -webkit-transform-origin: top;
    -ms-transform-origin: top;
        transform-origin: top;
    -webkit-transform: rotateX(-90deg);
    transform: rotateX(-90deg);
}
div.card div.flap1 {
    -webkit-transition: all 0.3s 0.3s ease-out;
    -o-transition: all 0.3s 0.3s ease-out;
    transition: all 0.3s 0.3s ease-out;
    z-index: -1;
}
div.card div.flap2 {
    -webkit-transition: all 0.3s 0s ease-out;
    -o-transition: all 0.3s 0s ease-out;
    transition: all 0.3s 0s ease-out;
    z-index: -2;
}
div.cards.showing div.card {
    cursor: pointer;
    opacity: 0.6;
    -webkit-transform: scale(0.88);
    -ms-transform: scale(0.88);
        transform: scale(0.88);
}
.no-touch div.cards.showing div.card:hover {
    opacity: 0.94;
    -webkit-transform: scale(0.92);
    -ms-transform: scale(0.92);
        transform: scale(0.92);
}
div.card.show {
    opacity: 1 !important;
    -webkit-transform: scale(1) !important;
    z-index: 20;
    -ms-transform: scale(1) !important;
        transform: scale(1) !important;
}
div.card.show div.card-title a.toggle-info {
    background: #ff6666 !important;
}
div.card.show div.card-title a.toggle-info span {
    top: 15px;
}
div.card.show div.card-title a.toggle-info span.left {
    right: 10px;
}
div.card.show div.card-title a.toggle-info span.right {
    left: 10px;
}
div.card.show div.card-flap {
    background: #ffffff;
    -webkit-transform: rotateX(0deg);
    transform: rotateX(0deg);
}
div.card.show div.flap1 {
    -webkit-transition: all 0.3s 0s ease-out;
    -o-transition: all 0.3s 0s ease-out;
    transition: all 0.3s 0s ease-out;
}
div.card.show div.flap2 {
    -webkit-transition: all 0.3s 0.2s ease-out;
    -o-transition: all 0.3s 0.2s ease-out;
    transition: all 0.3s 0.2s ease-out;
}

.aboutPanel {
    background-color: rgba(200,200,200,0.4);
    border-radius: 30px;
    border-style: solid;
    border-color: rgb(150,100,100);
    padding: 20px;
}


@media only screen and (max-width: 400px) {

    div.card {
        background: #F04F54;
        max-width: 300px;
    }

    div.card img {
        max-width: 270px;
    }
}





