
*, *:before, *:after {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

* {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
}

*:focus {
    outline: none !important;
}

body, html {
    height: 95%;
}


body {
    /*
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-line-pack: center;
    align-content: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    */
    padding: 12px;
    background: #2d2d2d;
    color: white;
    font-family: "brandon-grotesque", "Brandon Grotesque", "Source Sans Pro", "Segoe UI", Frutiger, "Frutiger Linotype", "Dejavu Sans", "Helvetica Neue", Arial, sans-serif;
    font-size: 24px;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
}


.bodyContainer {
    margin-top: 15%;
}

::-moz-selection {
    background: none;
}

::selection {
    background: none;
}

.flashy>a {
    display: block;
    cursor: pointer;
    -webkit-animation: pulseColor 1000ms linear 3100ms forwards;
    animation: pulseColor 1000ms linear 3100ms forwards;
    font-weight: 500;
}
.flashy>a:hover b, .flashy>a:focus b {
    color: #F04F54;
}
.flashy>a:active b {
    color: #43CB9D;
}


a {
    color: #43CB9D;
    cursor: pointer;
  /*  -webkit-animation: pulseColor 1000ms linear 3100ms forwards;
    animation: pulseColor 1000ms linear 3100ms forwards;
   */ font-weight: 500;
}
a:hover, a:focus {
    color: #F04F54 !important;
}
a:active {
    color: #F04F54;
}

svg {
    width: 50px;
    height: auto;
}

.crack {
    position: relative;
    z-index: 4;
}
.crack polyline {
    fill: none;
    stroke: #F04F54;
    stroke-width: 10px;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-dasharray: 1649.099;
    stroke-dashoffset: 1649.099;
    -webkit-animation: drawStroke 1500ms ease-out 500ms forwards;
    animation: drawStroke 1500ms ease-out 500ms forwards;
}

main {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-line-pack: center;
    align-content: center;
    -ms-flex-pack: center;
    justify-content: center;
}
main > div {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-line-pack: center;
    align-content: center;
    position: relative;
    overflow: hidden;
}
main > div svg {
    position: relative;
    z-index: 1;
}
main > div svg polygon {
    fill: #343434;
}
main > div span {
    display: block;
    position: relative;
    z-index: 0;
    padding: 0 12px;
    line-height: 1.4;
}
main > div:first-child {
    text-align: right;
    z-index: 1;
}
main > div:first-child span:first-child {
    opacity: 0;
    -webkit-transform: translateX(100%);
    -ms-transform: translateX(100%);
        transform: translateX(100%);
    -webkit-animation: translateLeft 1000ms linear 1250ms forwards;
    animation: translateLeft 1000ms linear 1250ms forwards;
}
main > div:first-child span:last-child {
    opacity: 0;
    -webkit-transform: translateX(100%);
    -ms-transform: translateX(100%);
        transform: translateX(100%);
    -webkit-animation: translateLeft 1000ms linear 1450ms forwards;
    animation: translateLeft 1000ms linear 1450ms forwards;
}
main > div:first-child svg polygon {
    -webkit-animation: removeFill 10ms ease-out 1600ms forwards;
    animation: removeFill 10ms ease-out 1600ms forwards;
}
main > div:last-child {
    z-index: 0;
}
main > div:last-child span:first-child {
    opacity: 0;
    -webkit-transform: translateX(-100%);
    -ms-transform: translateX(-100%);
        transform: translateX(-100%);
    -webkit-animation: translateRight 1000ms linear 1650ms forwards;
    animation: translateRight 1000ms linear 1650ms forwards;
}
main > div:last-child span:last-child {
    opacity: 0;
    -webkit-transform: translateX(-100%);
    -ms-transform: translateX(-100%);
        transform: translateX(-100%);
    -webkit-animation: translateRight 1000ms linear 1850ms forwards;
    animation: translateRight 1000ms linear 1850ms forwards;
}

@-webkit-keyframes drawStroke {
    0% {
        stroke-dashoffset: 1649.099;
    }
    100% {
        stroke-dashoffset: 0;
    }
}

@keyframes drawStroke {
    0% {
        stroke-dashoffset: 1649.099;
    }
    100% {
        stroke-dashoffset: 0;
    }
}
@-webkit-keyframes removeFill {
    0% {
        fill: #343434;
    }
    100% {
        fill: rgba(52, 52, 52, 0);
    }
}
@keyframes removeFill {
    0% {
        fill: #343434;
    }
    100% {
        fill: rgba(52, 52, 52, 0);
    }
}
@-webkit-keyframes pulseColor {
    0% {
        color: #43CB9D;
    }
    25% {
        color: #F04F54
    }
    50% {
        color: #43CB9D;
    }
    75% {
        color: #F04F54
    }
    100% {
        color: #43CB9D;
    }
}
@keyframes pulseColor {
    0% {
        color: #43CB9D;
    }
    25% {
        color: #F04F54
    }
    50% {
        color: #43CB9D;
    }
    75% {
        color: #F04F54
    }
    100% {
        color: #43CB9D;
    }
}
