    /* margin-top: -4%; */
    position: absolute;
    top: -330px;
    width: 2000px;
    left: -500px;
}

/* The navigation bar */
.navbar {
    overflow: hidden;
    background-color: #1a1a1a;
    position: inherit; /* Set the navbar to fixed position */
    top: -50px; /* Position the navbar at the top of the page */
    width: 100%; /* Full width */
}

/* Links inside the navbar */
.navbar a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

/* Change background on mouse-over */
.navbar a:hover {
    background: #ddd;
    color: black;
}

